<template>
  <div class="payDetail">
    <div class="top">
      <div v-if="$route.query.type == '1'">
        <img
          :src="$route.query.result == '1'?require('@/assets/img/pic/paySuccess.jpg'):require('@/assets/img/pic/payFail.jpg')"
          alt
        />
        <p class="text" v-if="$route.query.result == '1'">向{{$route.query.shopName}}商家支付成功</p>
      </div>
      <div v-else>
        <img
          :src="$route.query.result == '1'?require('@/assets/img/pic/paySuccess.jpg'):require('@/assets/img/pic/payFail.jpg')"
          alt
        />
        <p class="text" v-if="$route.query.result == '1'">向{{$route.query.userName}}转移成功</p>
      </div>
      <p
        class="price"
        :style="$route.query.result == '1'?'':'margin-top:5px;'"
      >{{$route.query.result == '1'?$route.query.type == '1'?`￥${$route.query.balance}`:`${$route.query.balance} 金额`:'转移失败'}}</p>
      <div class="line"></div>
      <div
        class="btn"
        style="margin-bottom:10px;background:#40b724;"
        v-if="$route.query.result == '2'"
        @click="back"
      >重新支付</div>
      <div class="btn" @click="backIndex">返回首页</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      balance: ""
    };
  },
  methods: {
    back() {
      this.$router.back();
    },
    backIndex() {
      this.$router.replace({
        name: "beautySalonMallIndex"
      });
    }
  },
  mounted() {
    document.title = "付款";
  }
};
</script>
<style lang="scss" scoped>
.payDetail {
  background: #fff;
  height: 100vh;
  .top {
    text-align: center;
    background: #fff;
    padding: 20px 0;
    img {
      width: 50px;
      border-radius: 50%;
    }
    .text {
      line-height: 28px;
      color: #333;
      margin: 10px 0 0;
    }
    .price {
      line-height: 28px;
      font-size: 20px;
      color: #333;
      font-weight: bold;
    }
    .line {
      border-bottom: 1pt solid #eee;
      margin: 20px;
    }
    .btn {
      background: #ef5c54;
      color: #fff;
      text-align: center;
      height: 50px;
      line-height: 50px;
      font-size: 18px;
      border-radius: 50px;
      margin: 0 20px;
    }
  }
}
</style>


